footer{
    background-color: hsl(0, 0%, 98%);
    padding: 5% 10%;
    position: relative;
    overflow: hidden;
}
footer hr{
    position: relative;
    z-index: 2;
    border-top: 1px solid hsl(273, 4%, 51%);
    margin: 2rem 0;
}
footer div{
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
}
footer ul{
    list-style-type: none;
}
.sociallist li{
    cursor: pointer;
    display: inline-block;
}
.sociallist li:nth-child(n+2){
    margin-left: 15px;
}
.footerlinks li{
    text-transform: uppercase;
    font-weight: 700;
    line-height: 2;
}
.footerlinks li:first-child{
    color: hsl(273, 4%, 51%);
    margin: 1.5rem 0;
}
.footerlinks li:nth-child(n+2){
    color: hsl(256, 26%, 20%);
    cursor: pointer;
}
.footerlinks li:nth-child(n+2):hover{
    text-decoration: underline;
}
svg.social :hover{
    fill: hsl(256, 26%, 20%);
}
.desksvg{
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
}
.mobsvg{
    display: none;
}




@media screen and (max-width:770px) {
    footer{
        padding: 5rem 5%;
        text-align: center;
    }
    footer div{
        flex-direction: column;
    }
    .logo{
        margin-bottom: 2rem;
    }

    .desksvg{
        display: none;
    }
    .mobsvg{
        display: block;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
    }
}