header{
    width: 100%;
    background-color: hsl(256, 26%, 20%);
    position: relative;
}
.header-container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    padding: 5rem 0;
}
.header-container div{
    width: 50%;
}
.mobleftsvg, .mobrightsvg{
    display: none;
}
.header-container hr{
    width: 120px;
    border-top: 1px solid hsl(216, 30%, 68%);
    margin-bottom: 3rem;
}
.header-container h1{
    position: relative;
    z-index: 2;
    color: white;
    font-family: 'DM Serif Display', serif;
    font-size: 3.5em;
    font-weight: 400;
    line-height: 1;
}
.header-container p{
    color: hsl(216, 30%, 68%);
    line-height: 1.5;
    font-weight: 700;
    padding: 2rem 1rem 2rem 0;
}
.header-container button{
    position: relative;
    z-index: 2;
    background-color: transparent;
    color: hsl(216, 30%, 68%);
    padding: 10px 25px;
    border: 2px solid hsl(216, 30%, 68%);
    text-transform: uppercase;
    letter-spacing: 1.5px;
    font-weight: 700;
    cursor: pointer;
    margin-bottom: 1rem;
}
.header-container button:hover {
    color: hsl(256, 26%, 20%);
    background-color: white;
    border: 2px solid white;
}
.introimg{
    width: 50%;
    position: relative;
    display: flex;
    justify-content: flex-end;
}
.deskpic{
    position: absolute;
    width: 80%;
}
.mobpic{
    display: none;
}
.bg-pattern-intro-left-desktop{
    position: absolute;
    top: 65%;
}
.bg-pattern-intro-right-desktop{
    position: absolute;
    z-index: 0;
    right: 0;
    top: -25%;
}




@media screen and (max-width:980px) {
    .deskpic{
        width: 95%;
    }
}

@media screen and (max-width:770px) {
    .header-container{
        width: 100%;
        text-align: center;
        padding: 0 0 5rem;
    }
    .header-container{
        flex-direction: column-reverse;
    }
    .header-container div{ 
        position: relative;
        width: 90%;
        margin: 0 auto;
    }
    .header-container hr{
        display: none;
    }
    .header-container h1{
        margin-top: 3rem;
        font-size: 2.8rem;
    }
    .header-container .introimg{
        width: 100%;
    }
    .introimg{
        justify-content: flex-start;
    }

    .deskpic{
        display: none;
    }
    .mobpic{
        display: block;
        width: 100%;
    }

    .bg-pattern-intro-left-desktop{
        display: none;
    }
    .bg-pattern-intro-right-desktop{
        display: none;
    }

    .mobleftsvg, .mobrightsvg{
        display: block;
        position: absolute;
        z-index: 1;
    }
    .mobleftsvg{
        top: 0;
        left: -10%;
    }
    .mobrightsvg{
        top: 70%;
        right: -5%;
    }
}