nav{
    position: relative;
    z-index: 2;
    width: 100%;
    background-color: white;
}
.nav-container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem 0;
    color: hsl(216, 30%, 68%);
}
nav ul{
    list-style-type: none;
}
nav li{
    display: inline-block;
    margin-left: 25px;
    text-transform: uppercase;
    font-weight: 700;
}
nav li:hover{
    cursor: pointer;
    color: hsl(256, 26%, 20%);
    font-weight: 700;
}
.navbtn{
    color: hsl(256, 26%, 20%);
    padding: 5px 10px;
    border: 2px solid hsl(256, 26%, 20%);
}
.navbtn:hover{
    color: white;
    background-color: hsl(256, 26%, 20%);
}
.mobile-menu{
    display: none;
}
.hamburger{
    display: none;
}



@media screen and (max-width:770px) {
    .hamburger{
        display: block;
    }
    .mobile-menu{
        overflow: hidden;
        position: absolute;
        left: 0;
        color: white;
        background-color: #fff;
        width: 200px;
        text-align: center;
        padding: 2rem 2rem 0;
        width: 100%;
        background-color: hsl(270, 9%, 17%);
    }
    nav li:hover{
        cursor: pointer;
        color: white;
    }
    .navimg{
        width: 130%;
        position: relative;
        left: -15%;
    }
    /* shows the menu when the toggle is working */
    .show .mobile-menu{
        display: block;
        top: 5rem;
    }
    .mobile-menu li{
        display: block;
        margin: 1rem 0.5rem;
        font-weight: 400;
    }
    .mobile-menu li:last-child{
        margin: 3rem 0 0;
    }
    .menu{
        display: none;
    }
    .mobnavbtn{
        padding: 5px 10px;
        border: 2px solid white;
    }    
}