h2, h3{
    font-family: 'DM Serif Display', serif;
    color: hsl(270, 9%, 17%);
}
main{
    width: 100%;
    padding-top: 15rem;
}
.container{
    width: 80%;
    margin: 0 auto;
}
.parts{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
    padding-bottom: 8rem;
    justify-content: space-between;
}
.attributes hr{
    width: 120px;
    border-top: 2px solid hsl(216, 30%, 68%);
    margin-bottom: 3rem;
}
.attributes h2{
    font-size: 3rem;
    font-weight: 400;
    margin-bottom: 4rem;
}
.parts h3{
    font-size: 1.6rem;
    font-weight: 400;
    margin: 2rem 0 1.2rem;
}
.parts p{
    color: hsl(216, 30%, 68%);
    font-size: 1.1rem;
    line-height: 1.5;
}

/* box  */
.box{
    background-color: hsl(256, 26%, 20%);
    position: relative;
    overflow: hidden;
    margin-bottom: 8rem;
}
.box section{
    padding: 5% 8%;
    display: flex;
    justify-content: space-between;
}
.box h2{
    font-size: 3rem;
    color: white;
    font-weight: 400;
}
.desk-boxsvg{
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
}
.mob-boxsvg{
    display: none;
}

.btncontainer{
    display: flex;
    justify-content: center;
    align-items: center;
}
.btncontainer button{
    cursor: pointer;
    z-index: 2;
    background-color: transparent;
    border: 2px solid white;
    color: white;
    text-transform: uppercase;
    padding: 10px 20px;
    letter-spacing: 3px;
    font-weight: 700;
}
.btncontainer button:hover{
    color: hsl(256, 26%, 20%);
    background-color: white;
}



@media screen and (max-width:770px){
    main{
        padding-top: 10rem;
        text-align: center;
    }
    .container{
        width: 90%;
    }
    .parts{
        grid-template-columns: 1fr;
    }
    .attributes hr{
        margin: 0 auto;
    }
    .attributes h2{
        margin: 2rem 0;
        font-size: 2.5rem;
    }
    .parts section{
        margin: 2rem 0;
    }



    /* box */

    .box section{
        flex-direction: column;
    }
    .box h2{
        position: relative;
        z-index: 2;
        font-size: 2rem;
        margin: 3rem 0 2rem;
    }
    .btncontainer button{
        margin-bottom: 3rem;
    }
    .desk-boxsvg{
        display: none;
    }
    .mob-boxsvg{
        display: block;
        position: absolute;
        z-index: 1;
        right: 0;
        top: 0;
    }
}